body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #48b0c7;
  color: #473857;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.success {
  color: green;
  background: rgb(240, 236, 236);
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 13px;
  margin-bottom: 13px;
}
.error {
  color: red;
  background: rgb(240, 236, 236);
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 13px;
  margin-bottom: 13px;
}

h2 {
  text-align: center;
  color: #eec979;
  padding: 0;
}
.input {
  text-align: center;
  margin: 0.5em;
}
.button {
  color: #9ec084;
  font-style: "italic";
  background-color:black;
  margin-left: 0.1em;
}
.deletebutton{
  color: #D96738;
  font-style: "italic";
  background-color:black;
  margin-left: 0.1em;
}
.person{
  text-align: center;
  margin: 0.5em;
}